<template>
    <div class="views news-detail">
        <navigate :title="data.infoType || '资讯详情'" position="fixed" color="#fff" backgroundColor="#3899D7"/>
        <div class="scroll-wrapper">
            <div class="header">
                <div class="news-title">
                    {{data.title}}
                </div>
                <div class="news-source">
                    {{data.publisher}}<span class="time">{{data.createTime | formatPast}}</span>
                </div>
            </div>
            <div class="detail" v-html="data.content" ref="detail"></div>
        </div>
    </div>
</template>

<script>
    import Navigate from "../../base/navigate/navigate"
    import ImagePreview from "vant/lib/image-preview"
    import "vant/lib/image-preview/style"
    import {getNewDetail} from "../../api/news"
    import formatTime from "../../common/utils/time"

    export default {
        name: "newsDetail",
        data() {
            return {
                data: {
                    title: '',
                    publisher: '',
                    publish_time: '',
                    content: '',
                    type: ''
                }
            };
        },
        props: {
            id: {
                type: String,
                default: ''
            }
        },
        activated() {
            this.getNewDetail()
        },
        methods: {
            getNewDetail() {
                getNewDetail(this.id).then(res => {
                    this.data = res

                    this.$nextTick(() => {
                        let images, imageList = this.$refs.detail.querySelectorAll('img')

                        images = Array.prototype.map.call(imageList, (img, index) => {
                            img.addEventListener('click', event => {
                                ImagePreview({
                                    images,
                                    startPosition: index
                                })
                            })

                            return img.getAttribute('src')
                        })
                    })
                })
            }
        },
        filters: {
            formatPast: formatTime.formatPast
        },
        components: {
            Navigate
        }
    }
</script>

<style lang="stylus">
    @import '../../common/stylus/variable.styl';

    .news-detail
        padding-top 46px
        background-color #fff
        > .scroll-wrapper
            width 100%
            height 100%
            overflow auto
            .header
                padding 10px 20px
                border-bottom 1px solid #ececec
                .news-title
                    color #333
                    padding 10px 0
                    font-size $font-size-lm
                    line-height 26px
                .news-source
                    font-size $font-size-ms
                    .time
                        margin-left 10px
            .detail
                padding 10px 13px
                line-height 1.74625
                color #333
                text-indent 2em
                font-size 16px
                text-align justify
                p
                    line-height 1.74625
                img
                    display block
                    margin 10px 0
                    max-width 100%
</style>



