import axios from '../common/js/axios'

/**
 * 新闻资讯头部按钮
 * @param params 无参数
 * @returns {*}
 */
export function getNewHeader(params) {
    const url = `/info/gettype`
    return axios.get(url, {params})
}

/**
 * 新闻资讯列表 all
 * @param params
 * @param params.title        标题 为空查全部
 * @param params.pageNumber   页码
 * @param params.pageSize     每页显示条数
 * @returns {*}
 */
export function getNewList(params) {
    const url = `/info/getlist`
    return axios.get(url, {params})
}

/***
 * 新闻资讯详情
 * @param id
 * @returns {*}
 */
export function getNewDetail(id) {
    const url = `/info/getcontent`
    return axios.get(url, {params: {id}})
}

/***
 * 新闻资讯详情
 * @param id
 * @returns {*}
 */
export function getByType(params) {
    const url = `/info/getbytype`
    return axios.get(url, {params})
}

/***
 * 分区域&全部查看航道通告
 * @param params
 * @param params.areaId  区域 ID
 * @return {AxiosPromise<any>}
 */
export function getbyarea(params) {
    const url = `/info/getbyarea`

    return axios.get(url, {params})
}







